body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
nav {
  padding: 0 1rem;
}
.content {
  margin: 0 auto;
  max-width: 1280px;
  min-height: calc(100vh - 64px - 70px);
}

.list {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.card {
  max-width: 300px;
  max-height: 300px;
  margin: 20px;
}
.activator {
  max-width: 400px;
  max-height: 200px;
}

.card-category {
  max-width: 300px;
}

.recipe-ul {
  display: flex;
  justify-content: space-around;

  width: 300px;
}

.recipe-el-parent {
  position: relative;
  margin: 100px 0;
}
.recipe-post {
  position: absolute;
  top: 0;
  left: -300px;
  font-size: 76px;
  width: 500px;
  transform: rotate(-90deg);

  opacity: 0.5;
}
.recipe-span {
  display: flex;
}
.recipe-leave {
  margin-bottom: 50px;
}
.intro {
  top: 80px;
  left: -195px;
  width: 300px;
}
.ingedients {
  top: 77px;
}
.htm {
  top: 143px;
}

.about-elems {
  margin-left: 30px;
  opacity: 0.9;
}
.about-leave {
  margin-top: 50px;
}
